/* import */
import React, { Component } from "react";
import ChatBot from "react-simple-chatbot";
import InfobotReview from "./InfobotReview";
import { ThemeProvider } from "styled-components";
/*     */
import a1 from "../../img/a1.jpg";
import a2 from "../../img/a2.jpg";
import a3 from "../../img/a3.jpg";
import a4 from "../../img/a4.jpg";
import a5 from "../../img/a5.jpg";
import a6 from "../../img/a6.jpg";
import a7 from "../../img/a7.jpg";
import "../../css/Infobot.css";

/* export */
class ChatbotSteps extends Component {
  render(props) {
    const config = {
      steps: steps, //
      headerTitle: "Greenee", // 
      className: "greeniChatbot", //(CSS)()
      hideUserAvatar: "true",
      placeholder: "     .",
      botDelay: 0,
      /* footerStyle: { background: "#efefef" }, */
      bubbleOptionStyle: { background: "#f39c12" },
      botAvatar: avatarArray[Math.floor(Math.random() * avatarArray.length)],
      // floating: "true", /*     */
      // floatingStyle: floatingStyle,
      //floatingIcon={greenihead},
      //submitButtonStyle:{},
      //inputStyle:{},
      //width: {},
      height: "100.5vh",
    };
    return (
      <>
        <ThemeProvider theme={lightTheme}>
          <div
            style={{
              marginRight: "-1px",
              marginTop: "0px",
              borderColor: "#fff",
            }}
          >
            <button
              className="chatbot_back_btn"
              onClick={() => {
                window.history.back();
              }}
            >
              {"<"}
            </button>
            <ChatBot {...config} />
          </div>
        </ThemeProvider>
      </>
    );
  }
}
export default ChatbotSteps;
/*  */
/* Math.random()  0 ~ 1   .  0  
     Math.random()   length .
 Math.random()    *  length    
Math.floor()    . */
