import { Form, Button } from "react-bootstrap";
import sendImage from "../../img/send-icon.png";
import React, { useState } from "react";

/*   */
const fewshot =
  "Greeni is a kind and talkative friend. \n\n  :  ?\nGreeni :   ,   .  !\n :  ?\nGreeni :   .   .";

/* history */
var history =fewshot;

/* config */
var config = {
 /*   model: "davinci:ft-personal-2022-12-07-08-51-09"  ,*/
     model: "text-davinci-003", 
  stop: ["\n", "->"],
  temperature: 0.5,
  max_tokens: 150,
  top_p: 1,
  frequency_penalty: 0.5,
  presence_penalty: 0,
};

/* api */
const { Configuration, OpenAIApi } = require("openai");
const configuration = new Configuration({
  apiKey: " ",
});
const openai = new OpenAIApi(configuration);

/* export */
function TalkbotFooter(props) {

  const [inputform, setinputform] = useState("");
  const handleChange = ({ target: { value } }) => setinputform(value);

  /*       (      )*/
  function inputClear() {
    setTimeout(function() {
      setinputform("");
    }, 10); //sec delay
  }

  /*   */
  function outputCreate() {
    var validater = document.querySelector(".input-form").value;
    var object = {};
    object[validater] = 1;

    /*   */
    var prompt_initial =
      " : " +
      document.querySelector(".input-form").value +
      "->" +
      " \nGreeni : ";
    var prompt = history + " \n" + prompt_initial;

    /*  */
    openai
      .createCompletion({
        prompt: prompt,
        model: config.model,
        //model:"text-ada-001",
        //model: "davinci:ft-personal-2022-12-04-12-58-21",
        stop: config.stop, // [ ]     .  4, 
        temperature: config.temperature, //  , 
        max_tokens: config.max_tokens, //    
        top_p: config.top_p, //     ,  down
        frequency_penalty: config.frequency_penalty, //       
        presence_penalty: config.presence_penalty, //        
      })
      .then((result) => {
        /*    */
        history = prompt + result.data.choices[0].text;
        console.log("history : ", history);
        /*     */
        props.setMessage(result.data.choices[0].text, false);
      })
}

  /*    */
  const handleSubmit = (event) => {
    /*  */
    event.preventDefault();
    /*    */
    props.setMessage(inputform, true);
    /*     */
    outputCreate();
    //alert(` : ${inputform}`);
  };

  return (
    <Form onSubmit={handleSubmit}>
      <footer>
        <Form.Control
          onChange={handleChange}
          type="inputform"
          name="inputform"
          value={inputform}
          placeholder="   ."
          className="input-form"
        />
        <Button
          variant="#fff"
          type="submit"
          className="input-button"
          onClick={inputClear}
        >
          <img className="send-icon" src={sendImage} />
        </Button>
      </footer>
    </Form>
  );
}
export default TalkbotFooter;
